import { $admin } from "./axios.js";

function getUserInfo(params) {
    return $admin().get(`/api/Work/userInfo`, params);
}
function getUserLogin(code) {
    return $admin().get(`/api/Work/login?code=${code}`);
}

function getUserPermission(userId) {
    return $admin().get(`/api/Manage/uer/${userId}/role`);
}

export { getUserLogin, getUserInfo, getUserPermission };
